@import 'settings/base.scss';
:root{
    --item-border: rgba(252, 213, 53, .05);
}
.container{
    max-width: 1940px;
    padding: 0 100px;
    margin: 0 auto;
    @media(max-width: 768px){
        padding: 0 20px;
    }
}
section{
    position: relative;
    padding: 50px 0;
} 
.btn{
    display: inline-block;
    padding: 16px 35px;
    text-align: center;
    color: $gold;
    background: rgba(246, 215, 88, .05);
    font-size: 20px;
    font-family: 'Manrope';
    font-weight: 800;
    border-radius: 45px;
    transition: all .3s ease-in-out;
    cursor: pointer;
    &:hover{
        background: rgba(246, 215, 88, .15);
    }
}
 
// ------ SECTION-TITLE ------
.section-title{
    font-size: clamp(25px, 3vw, 70px);
    font-family: 'Gilroy';
    font-weight: 600;
    margin-bottom: 33px;
    span{
        color: $gold;
    }
}
// ------ SECTION-TITLE ------

.logo{
    display: flex;
    align-items: center;
    column-gap: 16px;
    color: #fff;
    svg{

    }
    @media(max-width: 768px){
        svg{
            width: 16px;
            height: 16px;
        }
    }
    h1{
        font-size: clamp(15px, 2vw, 40px);
        text-transform: uppercase;
        font-family: 'Breymont';
        font-weight: 700;
        .logo-gold{
            color: $darkGold;
        }
        .logo-reverse{
            text-transform: lowercase;
        }
    }
}



// --------- HEADER ---------
.header{
    padding: 50px 0;
    @media(max-width: 768px){
        &{
            padding: 14px 0 28px 0;
        }
    }
    &__menu{
        display: flex;
        align-items: center;
        column-gap: 90px;
    }
    &__nav-list{
        display: flex;
        align-items: center;
        column-gap: 90px;
        li a{
            color: #fff;
            font-size: clamp(9px, 1.5vw, 24px);
            font-family: 'Manrope';
            opacity: .9;
            transition: all .3s ease-in-out;
            &:hover{
                opacity: 1;
            }
        }
    }

    @media(max-width: 768px){
        &__menu{
            column-gap: 25px;
        }
        &__nav-list{
            column-gap: 24px;
        }
    }

}



// --------- LAYOUT ---------
.layout{
    padding: 0 0 50px 0;
    .layout__content-top{
        padding: 0 0 30px 0;
        .swiper-wrapper{
            display: flex;
            align-items: center;
        }
        .swiper-slide:nth-child(even) {
            z-index: 2 !important;
        }
        .swiper-slide{
            pointer-events: none;
        }
        .swiper-slide-active{
            pointer-events: all;
        }
        .layout__slide{
            display: grid;
            grid-template: auto / 60% 40%;
            align-items: center;
            column-gap: 32px;
            background: #000;
            .layout__text{
                overflow: hidden;
                h1{
                    font-size: clamp(29px, 4vw, 80px);
                    margin-bottom: 30px;
                }
                p{
                    font-size: clamp(12px, 1.5vw, 24px);
                    font-family: 'Gilroy';
                    font-weight: 300;
                    line-height: 190%;
                    letter-spacing: 0.02em;
                    opacity: .7;
                    margin-bottom: 56px;
                }
                a{
                    transition: none;
                    &:hover{
                        background: rgba(246, 215, 88, .05);
                    }
                }
            }
            svg{
                width: 94%;
                height: 100%;
                transform-style: preserve-3d;
                perspective: 500px;
                overflow: hidden;
            }

            @media(max-width: 768px){
                &{
                    display: flex;
                    flex-direction: column-reverse;
                    align-items: center;
                    row-gap: 10px;
                    text-align: center;
                }
            }
        }
    }

    .layout__content-bottom{
        display: flex;
        flex-direction: column;
        row-gap: 130px;
        align-items: center;
        @media(max-width: 768px){
            &{
                row-gap: 70px;
            }
        }
        .layout__reach{
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            h4{
                font-size: 40px;
                font-family: 'Gilroy';
                font-weight: 600;
                @media(max-width: 768px){
                    &{
                        font-size: 22px;
                    }
                }
            }
            .layout__btn{
                display: flex;
                align-items: center;
                column-gap: 18px;
                margin: 88px 0 44px 0;
                @media(max-width: 768px){
                    &{
                        margin: 38px 0 20px 0;
                    }
                }
                a{
                    padding: 12px 34px;
                    font-size: clamp(18px, 2.2vw, 40px);
                    border-radius: 12px;
                    background: $gold;
                    color: #070709;
                    font-weight: 800;
                }
            }
            p{
                font-size: 22px;
                font-family: 'Gilroy';
                font-weight: 300;
                line-height: 190%;
                opacity: .7;
                @media(max-width: 768px){
                    &{
                       font-size: 11px;
                    }
                }
            }
        }
        .layout__publishers{
            display: flex;
            flex-direction: column;
            align-items: center;
            row-gap: 18px;
            text-align: center;
            h4{
                font-size: 40px;
                font-family: 'Gilroy';
                font-weight: 600;
            }
            p{
                font-size: 22px;
                font-family: 'Gilroy';
                font-weight: 300;
                line-height: 190%;
                opacity: .7;
                @media(max-width: 768px){
                    &{
                       font-size: 11px;
                    }
                }
                span{
                    color: $gold;
                }
            }
        }
    }



    .swiper-button-prev{
        background: url('../images/layout/prev-arr.svg') center/cover no-repeat;
        width: 17px;
        height: 30px;
        left: -60px;
        top: 40%;
        &::after{
            display: none;
        }
    }
    .swiper-button-next{
        background: url('../images/layout/next-arr.svg') center/cover no-repeat;
        width: 17px;
        height: 30px;
        right: -60px;
        top: 40%;
        &::after{
            display: none;
        }
    }
}


.monetize{

    &__main{
        display: grid;
        grid-template: auto / 60% 40%;
        align-items: center;
        column-gap: 80px;
        .monetize__main-text{
            p{
                font-family: 'Gilroy';
                font-size: 21px;
                font-weight: 300;
                line-height: 193%;
                opacity: .7;
            }
        }
        @media(max-width: 768px){
            .monetize__main-text{
                text-align: center;
                p{
                    font-size: 12px;
                }
            }
        }
        svg{
            width: 100%;
            height: 100%;
        }
        @media(max-width: 1200px){
            &{
                display: flex;
                flex-direction: column-reverse;
                align-items: center;
            }
        }
    }

    &__formats{


        .monetize__formats-text{
            margin: 40px 0 70px 0;
            text-align: center;
            h3{
                font-family: 'Gilroy';
                font-size: clamp(25px, 2.2vw, 42px);
                margin-bottom: 8px;
            }
            p{
                font-family: 'Gilroy';
                font-size: 22px;
                font-weight: 300;
                opacity: 0.8;
            }
            @media(max-width: 768px){
                &{
                    margin: 50px 0 20px 0;
                }

            }
        }

        .monetize__formats-list{
            max-width: 1250px;
            margin: 0 auto;
            .monetize__formats-item{
                position: relative;
                padding: 30px 40px;
                margin-bottom: 57px;
                border: 1.07px solid var(--item-border);
                border-radius: 27px;
                overflow: hidden;
                h3{
                    position: relative;
                    z-index: 1;
                    font-family: 'Manrope';
                    font-size: 30px;
                    color: $gold;
                }
                p{
                    margin: 24px 0;
                    font-family: 'Gilroy';
                    font-weight: 300;
                    font-size: 21px;
                    line-height: 193%;
                    letter-spacing: 0.02em;
                    opacity: 0.8;
                }
                a{
                    display: block;
                    max-width: 185px;
                    margin-left: auto;
                }
                svg{
                    position: absolute;
                    top: 0;
                    left: 20%;
                    z-index: 0;
                }
                @media(max-width: 768px){
                    &{
                        text-align: center;
                    }
                    h3{
                        font-size: 20px;
                    }
                    p{
                        font-size: 12px;
                    }
                    a{
                        display: inline-block;
                        margin-left: 0;
                    }
                    svg{
                        display: none;
                    }
                }
            }
        }
    }

}


.perks{

    &__main{
        display: grid;
        grid-template: auto / 60% 40%;
        align-items: center;
        column-gap: 80px;
        margin-bottom: 50px;
        .perks__main-text{
            p{
                font-family: 'Gilroy';
                font-size: 21px;
                font-weight: 300;
                line-height: 193%;
                opacity: .7;
            }
        }
        @media(max-width: 768px){
            .perks__main-text{
                text-align: center;
                p{
                    font-size: 12px;
                }
            }
        }
        svg{
            width: 100%;
            height: 100%;
        }
        @media(max-width: 1200px){
            &{
                display: flex;
                flex-direction: column-reverse;
                align-items: center;
            }
        }
    }


    &__options{
        text-align: center;
        h3{
            color: $gold;
            font-size: clamp(20px, 2.2vw, 42px);
            font-family: 'Gilroy';
            font-weight: 600;
            margin-bottom: 12px;
        }
        h6{
            font-size: 22px;
            font-family: 'Gilroy';
            font-weight: 300;
            margin-bottom: 30px;
            opacity: 0.8;
        }
        p{
            font-size: 22px;
            font-family: 'Gilroy';
            font-weight: 300;
            line-height: 193%;
            opacity: 0.4;
        }
        @media(max-width: 768px){
            h6{
                font-size: 12px;
                margin-bottom: 15px;
            }
            p{
                font-size: 12px;
            }
        }
    }


    .form{
        max-width: 1040px;
        padding: 40px;
        margin: 150px auto 120px auto;
        background: #0D0D0F;
        border-radius: 28px;
        text-align: center;
        h2{
            color: #E3E8EE;
            font-size: clamp(30px, 3vw, 50px);
            margin-bottom: 10px;
        }
        &__select{
            display: grid;
            grid-template: auto / 14% 85%;
            column-gap: 11px;
        }
        &__group{
            margin-bottom: 35px;
            text-align: left;
            .form__label{
                display: inline-block;
                margin-bottom: 26px;
                color: #f4f4f4;
                font-size: 22px;
                font-family: 'Manrope';
                font-weight: 800;
            }
            input,
            textarea{
                position: relative;
                width: 100%;
                height: 70px;
                padding: 24px 30px;
                color: #d4d4d4;
                background: #1A1A1C;
                font-size: 16px;
                font-family: 'Manrope';
                border-radius: 20px;
                &::placeholder{
                    position: relative;
                    bottom: 0;
                    color: #f4f4f4;
                    font-size: 16px;
                    font-family: 'Manrope';
                    opacity: 0.25;
                }
                &:focus::placeholder{
                    opacity: 0;
                }
            }
            textarea{
                height: 214px;
                text-align: left;
                max-width: 100%;
                min-width: 100%;
                min-height: 70px;
                &::placeholder{
                    position: absolute;
                    left: 30px;
                    top: 24px;
                }
            }
        }
        &__btn{
            display: block;
            margin-left: auto;
            max-width: 125px;
            padding: 16px 36px;
            color: #000;
            background: $gold;
        }

        .choices{
            border-radius: 20px;
            height: 70px;
        }
        .choices.is-open{
            border-radius: 0;
        }
        
        .choices[data-type*=select-one]::after {
            border-color: transparent transparent transparent;
            top: 38%;
        }
        .choices[data-type*=select-one].is-open::after {
            border-color: transparent transparent transparent;
        }
        .choices{

            &::after{
                appearance: none;
            }
            &::after{
                content: '';
                margin: 0;
                background: url('../images/form/arr.svg') center/cover no-repeat !important;
                width: 16px;
                height: 22px;
            }
            &__inner{
                background: rgba(18, 18, 18, .9);
                border: none;
                height: 70px;
                display: flex;
                align-items: center;
                padding: 0 8px;
            }
            &__list{
                border: none;
                overflow-x: hidden;
            }
            &__item{
                color: #fff;
                background: rgba(18, 18, 18, 0.9);;
                font-size: 16px;
                font-family: 'Manrope';
                width: 200%;
            }
            .choices__item--selectable.is-highlighted{
                color: #fff;
                background: red;
            }
        }

        .error-label{
            font-size: 14px;
            margin-top: 6px;
            color: $darkGold;
            font-family: 'Gilroy';
        }

        @media(max-width: 1200px){
            &{
                margin: 80px auto 50px auto;
            }
            &__select{
                grid-template: auto / 50% 50%;
            }
        }
        @media(max-width: 475px){
            &{
                margin: 80px auto 50px auto;
            }
            &__select{
                grid-template: auto auto / 100%;
            }
        }
    }


    &__advertisers{
        text-align: center;
        h3{
            font-size: clamp(20px, 2.2vw, 42px);
            font-family: 'Gilroy';
            font-weight: 600;
            margin-bottom: 8px;
        }
        p{
            font-size: 22px;
            font-family: 'Gilroy';
            font-weight: 300;
            line-height: 193%;
            opacity: 0.7;
        }
        span{
            color: $gold;
        }
        @media(max-width: 768px){
            p{
                font-size: 12px;
            }
        }
    }

}


