.footer{
    padding: 150px 0;
    @media(max-width: 840px){
        &{
            padding: 80px 0 38px 0;
        }
    }
    .logo{
        margin-bottom: 50px;
    }

    &__line{
        width: 0;
        height: .2px;
        background: rgba(255, 255, 255, .8);
        margin-bottom: 55px;
        transition: all 1.2s ease;
    }

    &__nav{
        display: flex;
        max-width: 950px;
        justify-content: space-between;
        column-gap: 35px;
        .footer__list{
            display: flex;
            flex-direction: column;
            row-gap: 47px;
            font-family: 'Gilroy';
            h3{
                font-size: 30px;
                font-weight: 500;
                color: rgba(255, 255, 255, 0.8);
            }
            a{
                font-size: 22px;
                font-weight: 300;
                color: rgba(255, 255, 255, 0.5);
                transition: all .2s ease-in;
                &:hover{
                    color: rgba(255, 255, 255, 1)
                }
            }
            @media(max-width: 840px){
                &{
                    row-gap: 32px;
                }
                h3{
                    font-size: 14px;
                }
                a{
                    font-size: 11px;
                }
            }
        }
    }

}

